.widgetLg{
  flex: 2;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  padding: 20px;
}

.widgetLgTitle{
  font-size: 22px;
  font-weight: 600;
}

.widgetLgTable{
  width: 100%;
  border-spacing: 20px;
}

.widgetLgTh{
  text-align: left;
}

.widgetLgUser{
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
  font-weight: 600;
}

.widgetLgImg{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.widgetLgDate, .widgetLgAmount {
  font-weight: 300;
}



.widgetLgButton{
  padding: 5px 7px;
  border: none;
  border-radius: 10px;
}

.widgetLgButton.Approved{
  background-color: rgb(199, 249, 208);
  color: darkgreen;
}

.widgetLgButton.Declined{
  background-color: rgb(250, 190, 190);
  color: darkred;
}

.widgetLgButton.Pending{
  background-color: rgb(209, 215, 255);
  color: darkblue;
}
